import React from 'react';

export const SummaryBlock = ({count = 0, memoryWords = 0 , perception = 0 , shulte = 0, shulteRed = 0}) => {
  return (<div>
    <div>
       : {shulte}
    </div>
    <div>
        : {shulteRed}
    </div>
    <div>
       : {perception}
    </div>
    <div>
      : {count}
    </div>
    <div>
       : {memoryWords}
    </div>
  </div>);
}

